<section class="article-intro-block"><div class="article-intro-block__wrapper"><div class="article-intro-block__image-wrapper"><img class="article-intro-block__image" :src="componentData.data.image" alt="Изображение фона article-intro-block"></div><div class="article-intro-block__content"><h2 class="article-intro-block__title">{{ componentData.data.title }}</h2><div class="article-intro-block__statistic"><p class="article-intro-block__statistic-item"><img src="../assets/img/icons/icon-time.svg" alt="Иконка статистики Время" width="16" height="16"><span>{{ componentData.data['reading_time'] }} мин время чтения</span></p><p class="article-intro-block__statistic-item"><img src="../assets/img/icons/icon-eye.svg" alt="Иконка статистики Просмотры" width="16" height="16"><span>{{ componentData.data['views_count'] }} прочитали статью</span></p></div><p class="article-intro-block__desc">{{ componentData.data['short_description'] }}</p></div></div></section>