<div class="app-form"><div class="app-form__image-wrapper"><img class="app-form__image" src="../assets/img/cta-form-image.jpg" alt="Изображение на форме обратной связи"></div><form class="app-form__form"><h3 class="app-form__title">Стань участником проекта</h3><div class="app-form__inputs"><UiInput class="app-form__input" type="text" placeholder="Имя"></UiInput><UiInput class="app-form__input" type="tel" placeholder="Телефон"></UiInput><UiInput class="app-form__input" type="email" placeholder="Email"></UiInput></div><UiButton class="app-form__button" @click.prevent="formSubmit" buttonType="basic"><template #desc>Присоединиться</template></UiButton></form></div>